import React from "react";

import Container from "../../components/container";
import styles from "./index.module.css";

export default ({data}) =>
  <div>
    <section className={styles.about}>
      <Container>        
        {/* <h1 className={styles.title}>Hello!<br/> I am <strong>Henrique Craveiro</strong></h1>
        <h2 className={styles.subtitle}>Full stack web developer, amateur photographer and 
adventure sports lover.</h2> */}
      </Container>
    </section>  
    <Container>
      <h1>About {data.site.siteMetadata.title}</h1>
      <p>Ol√° eu sou Henrique Craveiro, trabalho a 4 anos com desenvolvimento de produtos digitais</p>
    </Container>
  </div>

export const query = graphql`
  query AboutQuery {
    site {
      siteMetadata {
        title
      }
    }
  }
`


